
import { _decorator, Component, Node } from 'cc';
import { PoolManager } from './PoolManager';
const { ccclass, property } = _decorator;
 
@ccclass('RedEnemySpawner')
export class RedEnemySpawner extends Component {

    redTimeBetweenSpawn: number = Math.random() * 7 + 3;
    redCurSpawnTime: number = 0; 

    start () {
        // [3]
    }

    update (deltaTime: number) {
        
        //spawn obstacles, one after the other
        this.redCurSpawnTime += deltaTime;

        //spawn red first // --TODO: RECALCULATE SPAWN TIME--
        if (this.redCurSpawnTime > this.redTimeBetweenSpawn)
        {
            PoolManager.Instance.createRedEnemy(this.node);
            this.redCurSpawnTime = 0;
            //this.redTimeBetweenSpawn = Math.random() * 10 + 3;
        }
    }
}